import React, { useState, useEffect, useContext } from 'react';
import { getFirestore, collection, query, orderBy, onSnapshot } from 'firebase/firestore';
import { UserInfoContext } from '../contexts/UserInfoContext';
import { useNavigate } from 'react-router-dom';

export default function Gallery() {
  const [photos, setPhotos] = useState([]);
  const db = getFirestore();
  const navigate = useNavigate(); // navigate  

  // Firestore   
  useEffect(() => {
    const q = query(collection(db, 'gallery'), orderBy('createdAt', 'desc'));

    // onSnapshot     
    const unsubscribe = onSnapshot(q, (snapshot) => {
      const list = snapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data(),
      }));
      setPhotos(list); //    
    });

    //  unmount    clean-up 
    return () => unsubscribe();
  }, [db]);

  return (
    <div className="gallery-container max-w-7xl mx-auto px-4 py-8">
      <h1 className="text-center text-2xl font-bold mb-6"></h1>

      {/*    */}
      <div className="flex justify-end mb-4">
        <button
          className="bg-sky-600 text-white px-4 py-2 rounded"
          onClick={() => navigate('/gallery/upload')} //   
        >
           
        </button>
      </div>

      {/*    */}
      {photos.length === 0 ? (
        <p className="text-center">   .</p>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {/*   */}
          {photos.map(photo => (
            <div key={photo.id} className="relative border rounded-lg overflow-hidden shadow-lg">
              <img
                src={photo.url}
                alt={photo.title}
                className="w-full h-48 object-cover rounded-t-lg"
                onClick={() => {
                  console.log(`Navigating to /gallery/${photo.id}`); // 
                  navigate(`/gallery/${photo.id}`); //    
                }}
              />
              <div className="absolute top-0 left-0 right-0 bottom-0 bg-black bg-opacity-50 p-4 text-white">
                <h3 className="text-lg">{photo.title}</h3>
                <p>{photo.description}</p>
                {photo.hashtags?.length > 0 && (
                  <div className="mt-2 text-xs text-blue-400">
                    {photo.hashtags.map((tag, index) => (
                      <span key={index} className="mr-2">#{tag}</span>
                    ))}
                  </div>
                )}
                <button
                  className="mt-2 bg-red-500 hover:bg-red-600 text-white px-4 py-2 rounded"
                  onClick={() => navigate(`/gallery/${photo.id}`)} //    
                >
                   
                </button>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
